/* === assets/css/components/kpi-tiles.css === */
/* ==========================================================================
   KPI Tiles Component - Clean & Readable Edition
   ========================================================================== */

/* KPI Tiles Container */
.kpi-tiles {
    display: flex;
    gap: 12px;
    align-items: stretch;
    height: var(--dashboard-topline-height, 82px);
    margin: 0;
    justify-content: flex-start;
    flex-wrap: nowrap;
}

/* KPI Tile - Clean Card Style */
.kpi-tile {
    width: 140px;
    height: 82px;
    box-sizing: border-box;
    /* Clean, deep background */
    background: linear-gradient(145deg,
        rgba(15, 30, 56, 0.85) 0%,
        rgba(10, 21, 40, 0.85) 100%);
    border: 1px solid rgba(0, 214, 137, 0.35);
    border-radius: 8px;
    padding: 10px 12px;
    position: relative;
    cursor: pointer;
    transition: all 0.2s ease;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
    overflow: hidden;
    user-select: none;
}

.kpi-tile:hover {
    background: linear-gradient(145deg,
        rgba(20, 38, 72, 0.9) 0%,
        rgba(15, 26, 48, 0.9) 100%);
    border-color: var(--emerald-400);
    box-shadow: 0 4px 12px rgba(0, 214, 137, 0.2);
    transform: translateY(-1px);
}

.kpi-tile-content {
    width: 100%;
    height: 100%;
    position: relative;
}

.kpi-tile-layer {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2px;
    width: 100%;
    height: 100%;
    text-align: center;
    padding: 0;
    overflow: hidden;
}

.kpi-tile-layer.active {
    display: flex;
}

/* Typography - Readable hierarchy with Cormorant Garamond */
.kpi-label {
    font-family: var(--font-body), 'Cormorant Garamond', Georgia, serif;
    font-size: 0.7rem;
    font-weight: 600;
    color: rgba(200, 215, 230, 0.9);
    text-transform: uppercase;
    letter-spacing: 0.03em;
    text-align: center;
    line-height: 1.15;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
}

.kpi-value {
    font-family: var(--font-body), 'Cormorant Garamond', Georgia, serif;
    font-size: 1.4rem;
    font-weight: 700;
    color: #f0f8fa;
    line-height: 1.1;
    letter-spacing: 0.01em;
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.4);
    text-align: center;
    white-space: nowrap;
}

.kpi-subtext {
    font-family: var(--font-body), 'Cormorant Garamond', Georgia, serif;
    font-size: 0.65rem;
    font-weight: 500;
    color: var(--emerald-400);
    letter-spacing: 0.01em;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
    line-height: 1.15;
}

/* Status Colors */
.kpi-value.positive { color: var(--emerald-400); }
.kpi-value.negative { color: #ff5a6e; }

/* Responsive */
@media (max-width: 1200px) {
    .kpi-tile { width: 130px; height: 78px; }
    .kpi-value { font-size: 1.4rem; }
}

@media (max-width: 768px) {
    .kpi-tiles { gap: 8px; }
    .kpi-tile { width: 120px; height: 74px; padding: 8px 10px; }
    .kpi-value { font-size: 1.3rem; }
    .kpi-label { font-size: 0.7rem; }
}


