/* Status Badge - Collapsed (ensure prominence) */
body.boxscores-collapsed .status-badge {
    font-size: var(--text-md); /* Increased from 0.75rem for better readability */
    padding: 5px 12px;
    font-weight: var(--weight-bold);
}

/* ═══════════════════════════════════════════════════════════════════════════
